.reset-password {
  width: 100%;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
}

.reset-password-header {
  font-size: 2rem;
  text-align: center;
  text-transform: uppercase;
}

.reset-password-dataform {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.reset-password-dataform-email {
  padding: .8rem;
  margin: 1rem;
  border-radius: 2.5px;
  outline: none;
  border: none;
}

.reset-password-dataform-password {
  padding: .8rem;
  margin: 1rem;
  border-radius: 2.5px;
  outline: none;
  border: none;
  position: relative;
}

.reset-password-dataform-button {
  padding: .8rem;
  margin: 1rem;
  border-radius: 2.5px;
  outline: none;
  border: none;
}

.reset-password-container {
  width: 25rem;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.dont-have-acc {
  padding: .5rem;
}

.reset-password-show-pass {
  position: fixed;
  top: 53%;
  left: 70%;
}

.reset-password-show-old-password {
  position: fixed;
  top: 31%;
  left: 70%;
}